*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

.contenedor-loader {
  background-color: white;
  /* position: fixed; */
  width: 100vw;
  height: 100vh;
  z-index: 8;
  transition: all 1.3s;
  display: flex;
  align-items: center;
  justify-content: center;
}

img {
  width: 100%;
}

.loader,
.loader1,
.loader2 {
  position: relative;
}

.loader:after {
  content: "";
  width: 60px;
  height: 60px;
  background-color: #fe4c50;
  animation: girar 0.5s linear infinite;
  position: absolute;
  top: 0;
  left: -30px;
  border-radius: 3px;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.5);
}

.loader:before {
  content: "";
  width: 60px;
  height: 10px;
  background-color: rgba(0, 0, 0, 0.4);
  animation: sombra-animada 0.5s linear infinite;
  position: absolute;
  top: 70px;
  left: -30px;
  border-radius: 50%;
}

.loader1:after {
  content: "";
  width: 60px;
  height: 60px;
  background-color: #b5aec436;
  animation: girar1 0.5s linear infinite;
  position: absolute;
  top: 0px;
  left: -120px;
  border-radius: 3px;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.5);
}

.loader1:before {
  content: "";
  width: 60px;
  height: 10px;
  background-color: rgba(0, 0, 0, 0.4);
  animation: sombra-animada1 0.5s linear infinite;
  position: absolute;
  top: 70px;
  left: -120px;
  border-radius: 50%;
}

@keyframes girar {
  17% {
    border-bottom-right-radius: 3px;
  }
  25% {
    transform: translateY(9px) rotate(22.5deg);
  }
  50% {
    transform: scale(1, 0.9) translateY(18px) rotate(45deg);
    border-bottom-right-radius: 40px;
  }
  75% {
    transform: translateY(9px) rotate(67.5deg);
  }
  100% {
    transform: translateY(0) rotate(90deg);
  }
}

@keyframes sombra-animada {
  50% {
    transform: scale(1.3, 1);
  }
}

@keyframes girar1 {
  17% {
    border-bottom-right-radius: 5px;
  }
  25% {
    transform: translateY(9px) rotate(22.5deg);
  }
  50% {
    transform: scale(1, 0.9) translateY(18px) rotate(45deg);
    border-bottom-right-radius: 40px;
  }
  75% {
    transform: translateY(9px) rotate(67.5deg);
  }
  100% {
    transform: translateY(0) rotate(90deg);
  }
}

@keyframes sombra-animada1 {
  50% {
    transform: scale(1.3, 1);
  }
}

.loader2:after {
  content: "";
  width: 60px;
  height: 60px;
  background-color: #b5aec436;
  animation: girar2 0.5s linear infinite;
  position: absolute;
  top: 0px;
  right: -120px;
  border-radius: 3px;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.5);
}

.loader2:before {
  content: "";
  width: 60px;
  height: 10px;
  background-color: rgba(0, 0, 0, 0.4);
  animation: sombra-animada2 0.5s linear infinite;
